import { FC, ReactNode, useEffect, useRef } from "react";
import { useLocation } from "react-router-dom";
import { usePagesStore } from "../stores/pagesStore";
import { Scroller } from "../scroller";
import { useStatusStore } from "../stores/statusStore";
import { usePageId } from "../utils";
import { Pages } from "../pages";
import { SectionData } from "../types";

interface Props {
  id?: string;
  children: ReactNode;
}

const autoScrollWait = 700;

let scroller: Scroller | null = null;

const ScrollContainer: FC<Props> = ({ id, children }) => {
  const { scrollUpCount, scrollDownCount, sectionDetails } = useStatusStore();
  const [pages, editCount, editedSectionId, sourceSectionId] = usePagesStore((state) => [
    state.pages,
    state.editCount,
    state.editedSectionId,
    state.sourceSectionId,
  ]);

  const pageId: string = usePageId(Pages);
  const sectionData: SectionData[] | undefined = usePagesStore((state) => state.pages[pageId]);

  const location = useLocation();
  const containerRef = useRef<HTMLDivElement | null>(null);
  const autoScrollInterval = useRef(0);
  const isAutoScroll = useRef(false);

  const clearAutoScroll = () => {
    isAutoScroll.current = false;
  };

  useEffect(() => {
    if (!isAutoScroll.current) {
      const idx: number | undefined = sectionData?.reduce(
        (acc: number | undefined, section, idx) => (section.id === sectionDetails ? idx : acc),
        undefined
      );

      if (!idx) return;

      //isAutoScroll.current = true;
      //setTimeout(clearAutoScroll, autoScrollWait);
      scroller?.scrollTo(idx - 1);
    }
  }, [scrollUpCount]);

  useEffect(() => {
    if (!isAutoScroll.current) {
      const idx: number | undefined = sectionData?.reduce(
        (acc: number | undefined, section, idx) => (section.id === sectionDetails ? idx : acc),
        undefined
      );

      if (idx === undefined || idx === sectionData.length - 1) return;

      //isAutoScroll.current = true;
      //setTimeout(clearAutoScroll, autoScrollWait);
      scroller?.scrollTo(idx + 1);
    }
  }, [scrollDownCount]);

  useEffect(() => {
    if (!scroller) {
      scroller = new Scroller(`#${id}`);
    }
    if (containerRef.current) {
      scroller?.scrollTo(0);
    }
  }, [location]);

  useEffect(() => {
    const sourceElement = document.getElementById(sourceSectionId);
    if (sourceElement) {
      //sourceElement.scrollIntoView();
    }

    const element = document.getElementById(editedSectionId);
    if (element) {
      scroller?.updateAndScroll(editedSectionId);
    }
  }, [editCount, editedSectionId, sourceSectionId]);

  return (
    <div id={id ?? undefined} className="rec-main-scroll-container">
      {children}
    </div>
  );
};

export default ScrollContainer;
