import { FC, useRef } from "react";
import { SectionData } from "../types";
import SlidePanel, { Position } from "./SlidePanel";
import { getAuthorElement } from "../utils";
import tempPic from "../../images/temp_info.png";
import { names, starWars, uniqueNamesGenerator } from "unique-names-generator";
import Avatar from "boring-avatars";
import { Heart } from "@phosphor-icons/react";

interface Props {
  sectionData: SectionData | undefined;
  debugItems?: { name: string }[];
  overflowAuto?: boolean;
  icon?: "heart";
}

const Likes: FC<Props> = ({ sectionData, debugItems, overflowAuto, icon }) => {
  const arr = debugItems ? debugItems : new Array(Math.round(Math.random() * 100)).fill(null);

  const likes = useRef(
    arr.map((item) => {
      if (item) return item?.name;
      const shortName: string = uniqueNamesGenerator({
        dictionaries: [names],
      });
      const shortName2: string = uniqueNamesGenerator({
        dictionaries: [starWars],
      });

      return `${shortName} ${shortName2.split(" ").pop()}`;
    })
  );

  return (
    <div className="rec-avatar-list" style={{ overflowY: overflowAuto ? "auto" : "unset" }}>
      {likes.current.map((uname) => {
        return (
          <div key={uname} className="rec-avatar">
            <div className="rec-avatar-container">
              <Avatar name={uname} variant="marble" />
              {icon === "heart" ? <Heart className="rec-avatar-icon" color="var(--secondaryColor)" size="22px" weight="fill" /> : null}
            </div>{uname}
          </div>
        );
      })}
    </div>
  );
};

export default Likes;
