import { FC } from "react";
import { useLocation } from "react-router-dom";
import SlidePanel, { Position } from "./SlidePanel";
import { Camera, HandEye, HandSwipeRight, HandTap, MagicWand, MouseLeftClick } from "@phosphor-icons/react";

interface Props {
  icon: string;
  iconSize?: number;
  children: React.ReactNode;
}

const getIcon = (icon: string, iconSize: number = 20): JSX.Element => {
  switch (icon) {
    case "click":
      return <HandTap size={iconSize}/>;
    case "drag":
      return <HandSwipeRight size={iconSize}/>;
    case "camera":
      return <Camera size={iconSize}/>;
    default:
      return <HandEye size={iconSize}/>;
  }
};

const InstructionLine: FC<Props> = ({ icon, iconSize, children }) => {
  return (
    <div className="rec-instruction-line">
      <div className="rec-instruction-icon">{getIcon(icon, iconSize)}</div>
      <div className="rec-instruction-text">{children}</div>
    </div>
  );
};

export default InstructionLine;
