import { CSSProperties, FC, ReactNode } from "react";

export enum Position {
  LEFT = "rec-left",
  RIGHT = "rec-right",
  TOP = "rec-top",
  BOTTOM = "rec-bottom",
}

interface Props {
  open?: boolean;
  children: ReactNode;
  level?: number;
  css?: CSSProperties;
  slideCss?: CSSProperties;
  manualClose?: boolean;
  position?: Position;
  offset?: { x: string; y: string; width: string; height: string };
  onBackgroundClose?: () => void;
}

const SlidePanel: FC<Props> = ({ open, children, css, slideCss, position = Position.RIGHT, onBackgroundClose }) => {
  return (
    <>
      <div
        className={`rec-slide-panel${open ? " rec-open" : ""} ${position}`}
        style={{
          ...(css || {}),
        }}
        onClick={onBackgroundClose}
      >
        <div className="rec-slide-panel-container" style={{
          ...(slideCss || {}),
        }}>{children}</div>
      </div>
    </>
  );
};

export default SlidePanel;
