import { FC, useEffect, useRef, useState } from "react";
import { usePagesStore } from "../stores/pagesStore";
import { useStatusStore } from "../stores/statusStore";
import { SectionData } from "../types";
import ArtworkLaunchPopup from "./ArtworkLaunchPopup";
import IFrame from "./IFrame";
import PageSectionEditor from "./PageSectionEditor";

const observerOptions = {
  root: null,
  rootMargin: "-5px 0px -65px 0px",
};

const centerObserverOptions = {
  root: null,
  rootMargin: "-50% 0px -50% 0px",
};

const PageSection: FC<SectionData & { page?: string }> = (props) => {
  const { infoSide, sectionDetails, artworkShow } = useStatusStore();
  const { id, backgroundColor, page, cover } = props;

  const isInfoSide = !!infoSide[id ?? ""];

  const editorMode = usePagesStore((state) => state.editorMode);

  const [, setRender] = useState(0);

  const sectionRef = useRef<HTMLDivElement | null>(null);
  const isOnScreen = useRef(false);
  const isOnInfo = useRef(false);
  isOnInfo.current = isInfoSide;

  const handleCenterIntersect = (elements: IntersectionObserverEntry[]) => {
    checkIntersect(elements, true);
  };

  const handleIntersect = (elements: IntersectionObserverEntry[]) => {
    checkIntersect(elements);
  };

  const checkIntersect = (elements: IntersectionObserverEntry[], isCenter?: boolean | undefined) => {
    const intersection: IntersectionObserverEntry | null = elements.reduce(
      (acc: IntersectionObserverEntry | null, element) => (element.target === sectionRef.current && element.isIntersecting ? element : acc),
      null
    );
    if (isCenter) {
      if (intersection) {
        if (sectionDetails !== id) {
          useStatusStore.getState().setSectionDetails(id);
        }
      }
    } else {
      if (intersection) {
        if (!isOnScreen.current) {
          isOnScreen.current = true;
          setRender((x) => x + 1);
        }
      } else {
        if (isOnScreen.current) {
          isOnScreen.current = false;
          if (isOnInfo.current) {
            useStatusStore.getState().setInfoSide(id, false);
          }
          setRender((x) => x + 1);
        }
      }
    }
  };

  const observer = useRef(new IntersectionObserver(handleIntersect, observerOptions));
  const centerObserver = useRef(new IntersectionObserver(handleCenterIntersect, centerObserverOptions));

  useEffect(() => {
    if (sectionRef.current) {
      addListeners();
    }
    return () => removeListeners();
  }, []);

  const addListeners = () => {
    if (sectionRef.current) {
      observer.current.observe(sectionRef.current);
      centerObserver.current.observe(sectionRef.current);
    }
  };

  const removeListeners = () => {
    observer.current.disconnect();
    centerObserver.current.disconnect();
  };

  return (
    <section key={id} id={id} className="rec-page-section" style={{ background: backgroundColor ?? "white" }} ref={sectionRef}>
      {editorMode && !!page && !!id ? <PageSectionEditor pageId={page!} sectionId={id!} /> : null}
      {typeof cover === "string" ? (
        <IFrame
          url={cover}
          paused={!isOnScreen.current || !!artworkShow}
          onClick={() => {
            useStatusStore.getState().setInfoSide(id, true);
          }}
        />
      ) : (
        cover
      )}
      <ArtworkLaunchPopup open={isInfoSide} id={id} onClose={() => useStatusStore.getState().setInfoSide(id, false)} />
    </section>
  );
};

export default PageSection;
