import { BrowserRouter, Route, BrowserRouter as Router, Routes } from "react-router-dom";

import "../css/root.css";
import "../css/ui.css";

import { IconContext } from "@phosphor-icons/react";
import { useRef } from "react";
import { siteData } from "../tempData";
import Header from "./Header";
import ScrollContainer from "./components/ScrollContainer";
import { Pages } from "./pages";
import About from "./pages/About";
import Explore from "./pages/Explore";
import Home from "./pages/Home";
import NoMatch from "./pages/NoMatch";
import Profile from "./pages/Profile";
import { usePagesStore } from "./stores/pagesStore";
import { scrollContainerId, sitePath } from "./utils";
import SideSwitch from "./components/SideSwitch";
import MenuPanel from "./components/MenuPanel";
import ArtworkLaunchPopup from "./components/ArtworkLaunchPopup";
import ArtworkPopup from "./components/ArtworkPopup";
// import 'modern-normalize/modern-normalize.css';

// included conditional pico css directly so we don't have to use scss processing, and couldn't find in the npm package
// import "../css/pico.conditional.min.css"

const Root = () => {
  const firstRunInit = useRef(false);
  if (!firstRunInit.current) {
    console.log("Init");
    usePagesStore.getState().setAllPages(siteData);
    usePagesStore.getState().setOriginPages(siteData);
    firstRunInit.current = true;
  }

  return (
    <BrowserRouter basename={sitePath}>
      <IconContext.Provider
        value={{
          color: "black",
          size: 16,
          weight: "regular",
          mirrored: false,
        }}
      >
        <ScrollContainer id={scrollContainerId}>
          <Routes>
            <Route path="*" element={<NoMatch />} />
            <Route path={Pages.home.path} element={<Home />} />
            <Route path={Pages.explore.path} element={<Explore />} />
            <Route path={Pages.about.path} element={<About />} />
            <Route path={Pages.profile.path} element={<Profile />} />
          </Routes>
        </ScrollContainer>
        <MenuPanel />
        <Header />
        <SideSwitch />
        <ArtworkPopup />
      </IconContext.Provider>
    </BrowserRouter>
  );
};

export default Root;
