import { FC, useEffect, useRef } from "react";
import ReactDOM from "react-dom";

interface Props {
  fullscreen?: boolean;
  children?: React.ReactNode;
  onClose?: () => void;
}

const Popup: FC<Props> = ({ fullscreen, children, onClose }) => {
  const contentRef = useRef<HTMLDivElement | null>(null);
  useEffect(() => {
    contentRef.current?.focus();
  }, []);

  return ReactDOM.createPortal(
    <div
      className="rec-popup-backdrop rec-popup-portal"
      onClick={
        fullscreen
          ? undefined
          : (evt) => {
              if ((evt.target as HTMLDivElement).classList.contains("rec-popup-backdrop")) {
                onClose?.();
              }
            }
      }
      onKeyDown={
        !fullscreen
          ? (evt) => {
              if (evt.code === "Escape") onClose?.();
            }
          : undefined
      }
      ref={contentRef}
      tabIndex={-1}
    >
      {children}
    </div>,
    document.body
  );
};

export default Popup;
