import { JsxElement } from "typescript";

export interface Tab {
  id: string;
  count?: number;
  name: string;
}

export interface Point {
  x: number;
  y: number;
}

export interface Size {
  width: number;
  height: number;
}

export interface Rect extends Point, Size {}

export interface ImageData64 {
  width: number;
  height: number;
  data: string;
}

export interface ImageDataSetup extends Size {
  icon?: string;
  text: string;
  font?: string;
  fontSize?: number;
  padding?: number;
  selected?: boolean;
}

export enum Permission {
  CAMERA = "camera",
  MICROPHONE = "microphone",
  MOUSE = "mouse",
  KEYBOARD = "keyboard",
  TOUCH = "touch",
  FILE_UPLOAD = "file-upload",
  FILE_DOWNLOAD = "file-download",
  GEOLOCATION = "geolocation",
  ACCELEROMETER = "accelerometer",
  GYROSCOPE = "gyroscope",
  FULLSCREEN = "fullscreen",
  AMBIENT_LIGHT_SENSOR = "ambient-light-sensor",
}

export enum Requirement {
  MACHINE_MOBILE = "Mobile device",
  MACHINE_DESKTOP = "Desktop machine",
  MACHINE_STRONG = "Strong desktop machine",
  VR = "VR / AR headset",
  BROWSER_WEBKIT = "Webkit browser",
  BROWSER_CHROME = "Chrome browser",
  BROWSER_FIREFOX = "Firefox browser",
  BROWSER_SAFARI = "Safari browser",
}

export enum Interaction {
  MOUSE_MOVE = "Mouse movement",
  MOUSE_SCROLL = "Mouse scroll",
  MOUSE_DRAG = "Mouse drag",
  MOUSE_CLICK = "Mouse click",
  CAMERA = "Camera",
  MICROPHONE = "Microphone",
  GYROSCOPE = "Gyroscope",
  DEFAULT = "Default",
}

export interface PageData {
  [id: string]: SectionData[];
}

export interface Sponsors {
  slug: string;
  name: string;
  image?: string;
  url?: string;
}

export enum SectionType {
  Any = "any",
  Invitation = "invitation",
  Exhibition = "exhibition",
  Artwork = "artwork",
}

export type Author = string | { name: string; url: string };
export type Comment = { user: string; content: string };
export type Exhibition = { id: string; name: string };

export interface SectionData {
  id: string;
  type?: SectionType;
  backgroundColor?: string;
  iconStrokeColor?: string;
  iconFillColor?: string;
  menuActiveColor?: string;
  cover?: string | JSX.Element;
  content?: string | JSX.Element;
  iframePermissions?: { [value in Permission]: true };
  requirement?: { [value in Requirement]: true };

  title?: string;
  instructions?: string;
  description?: string;
  authors?: Author[];
  interactions?: (Interaction | string)[];
  requirements?: (Requirement | string)[];
  sponsors?: Sponsors[];
  likes?: number;
  comments?: Comment[];
  exhibitions?: Exhibition[];
}
