const isSafari = (() => {
  const userAgent = navigator.userAgent.toLowerCase();
  return userAgent.includes("safari") && !userAgent.includes("chrome") && !userAgent.includes("android");
})();

export class Scroller {
  scrollContainer: null | HTMLElement = null;
  items: Element[] = [];
  currentIndex = 0;
  scrollWaitInterval = 0;
  lastScrollPos = 0;
  autoScrollFreeze = false;
  isInitialized = false;
  
  constructor(scrollContainerQuery = "") {
    if (!scrollContainerQuery) {
      return;
    }

    this.scrollContainer = document.querySelector(scrollContainerQuery);
    if (this.scrollContainer) {
      this.items = Array.from(this.scrollContainer.children);
      this.enable();
    }

    window.addEventListener("resize", this.resize.bind(this));
  }

  enable() {
    if (this.scrollContainer && !this.isInitialized) {
      this.isInitialized = true;
      if (isSafari) {
        this.scrollContainer.addEventListener("scroll", this.onScroll.bind(this));
      } else {
        this.scrollContainer.addEventListener("scrollend", this.onScrollEnd.bind(this));
      }
    }
  }

  disable() {
    if (this.scrollContainer) {
      if (isSafari) {
        this.scrollContainer.removeEventListener("scroll", this.onScroll.bind(this));
      } else {
        this.scrollContainer.removeEventListener("scrollend", this.onScrollEnd.bind(this));
      }
    }
  }

  resize() {
    window.clearTimeout(this.scrollWaitInterval);
    this.scrollWaitInterval = window.setTimeout(this.checkScroll.bind(this), 10, true);
  }

  onScrollEnd() {
    this.checkScroll();
  }

  onScroll(evt: Event) {
    evt.preventDefault();
    window.clearTimeout(this.scrollWaitInterval);
    if (this.scrollContainer) {
      //this.scrollWaitInterval = window.setTimeout(this.checkScroll.bind(this), 200);
      this.checkScroll.bind(this);
    }
  }

  checkScroll(immediate = false) {
    this.items.forEach((item, idx) => {
      const bounds = item.getBoundingClientRect();
      const center = window.innerHeight / 2;
      if (bounds.y < center && center <= bounds.y + bounds.height) {
        this.scrollTo(idx, immediate);
      }
    });
  }

  updateAndScroll(id: string) {
    if (this.scrollContainer) {
      this.items = Array.from(this.scrollContainer.children);
      this.items.forEach((item, idx) => {
        if (item.id === id) {
          this.scrollTo(idx);
        }
      });
    }
  }

  scrollTo(childIdx: number, immediate = false) {
    this.currentIndex = childIdx;
    const item = this.items[childIdx];
    if (item) {
      this.autoScrollFreeze = true;
      this.items[this.currentIndex].scrollIntoView({
        behavior: immediate ? undefined : "smooth",
        block: "nearest",
      });
    }
  }
}
