import { create } from "zustand";

interface BreadcrumbItem {
  link: string;
  name: string;
}

interface StatusStoreProps {
  breadcrumb: BreadcrumbItem[];
  sectionDetails: string;
  isMenuOpen: boolean;
  infoSide: { [sectionId: string]: boolean };
  artworkLaunch: string | null;
  artworkShow: string | null;
  scrollUpCount: number;
  scrollDownCount: number;
  setMenuOpen: (isMenuOpen: boolean) => void;
  setInfoSide: (sectionId: string, isInfoSide: boolean) => void;
  setSectionDetails: (sectionDetails: string | undefined) => void;
  setArtworkLaunch: (artworkId: string | null) => void;
  setArtworkShow: (artworkId: string | null) => void;
  setScrollUp: () => void;
  setScrollDown: () => void;
}

export const useStatusStore = create<StatusStoreProps>((set) => ({
  breadcrumb: [],
  isMenuOpen: false,
  infoSide: {},
  sectionDetails: "",
  isEditorMode: false,
  artworkLaunch: null,
  artworkShow: null,
  scrollUpCount: 0,
  scrollDownCount: 0,
  setMenuOpen: (isMenuOpen: boolean) => set(() => ({ isMenuOpen })),
  setInfoSide: (sectionId: string, isInfoSide: boolean) =>
    set((state: StatusStoreProps) => ({ infoSide: { ...state.infoSide, [sectionId]: isInfoSide } })),
  setBreadcrumb: (breadcrumb: BreadcrumbItem[]) => set(() => ({ breadcrumb })),
  setSectionDetails: (sectionDetails: string | undefined) => set(() => ({ sectionDetails: sectionDetails ?? "" })),
  setArtworkLaunch: (artworkLaunch: string | null) => set(() => ({ artworkLaunch })),
  setArtworkShow: (artworkShow: string | null) => set(() => ({ artworkShow })),
  setScrollUp: () => set((state) => ({ scrollUpCount: state.scrollUpCount + 1 })),
  setScrollDown: () => set((state) => ({ scrollDownCount: state.scrollDownCount + 1 })),
}));
