import { HandEye } from "@phosphor-icons/react/dist/ssr";
import { Author, Point, SectionType } from "./types";
import { v4 } from "uuid";
import { Eye, Play } from "@phosphor-icons/react";
import { useLocation } from "react-router-dom";

// BEFORE DEPLOY:
// • Add to package.json: "homepage": "/common/recursive.gallery",
// • change sitePath to the same
export const sitePath = "/common/recursive.gallery";

export const CSS_MENU_BACKGROUND = "--iconFillColor";
export const CSS_MENU_ACTIVE = "--menuActiveColor";
export const CSS_MENU_COLOR = "--iconStrokeColor";

export const headerIconSize = 22;

export const setCssVar = (varName: string, value: string) => {
  document.documentElement.style.setProperty(varName, value);
};

export const randomMap = (min: number, max: number, allowNegative?: boolean) =>
  ((max - min) * Math.random() + min) * (allowNegative ? (Math.random() < 0.5 ? -1 : 1) : 1);

export const pxToRem = (px: number) => `${px / 16}rem`;

export const getDistance = (p1: Point, p2: Point) => {
  const dx = p2.x - p1.x;
  const dy = p2.y - p1.y;
  return Math.sqrt(dx * dx + dy * dy);
};

export const getId = () => v4();

export const toggleFullScreen = () => {
  if (!isFullscreen()) {
    fullscreenOn();
  } else {
    fullscreenOff();
  }
};

export const isFullscreen = () => !!document.fullscreenElement;

export const fullscreenOn = () => document.documentElement.requestFullscreen();

export const fullscreenOff = () => document.exitFullscreen();

export const scrollContainerId = "rec-main-container";

export const getAuthorElement = (authors: Author[] | undefined): JSX.Element[] | null =>
  authors
    ? authors.reduce((acc: JSX.Element[], author, idx, arr) => {
        const name = author instanceof Object ? author.name : author;
        //return `${acc}${!idx ? "" : idx === arr.length - 1 ? " and " : ", "}${name}`;
        return [
          ...acc,
          <span key={name}>
            {!idx ? "" : idx === arr.length - 1 ? <>&nbsp;and&nbsp;</> : <>,&nbsp;</>}
            <a href="/">{name}</a>
          </span>,
        ];
      }, [])
    : null;

export const getSectionActionIcon = (type: SectionType | undefined, path: string = "/") => {
  if (type === SectionType.Invitation) {
    return <Eye size={headerIconSize} />;
  }
  if (type === SectionType.Artwork) {
    return <Play size={headerIconSize} />;
  }
  return <></>;
};

//TODO: make type finally for pages
export const usePageId = (pages: { [id: string]: any }): string => {
  const location: string = useLocation().pathname;
  return Object.values(pages).reduce((acc, page) => (page.path === location ? page.id : acc), "");
};

// Call this function without parameter for toggle functionality
export const onFullscreen = (state?: boolean | undefined) => {
  if (state === undefined) {
    if (document.fullscreenElement) {
      document.exitFullscreen?.();
    } else {
      document.body.requestFullscreen?.();
    }
  } else {
    if (state) {
      document.body.requestFullscreen?.();
    } else {
      document.exitFullscreen?.();
    }
  }
};

export const debugRandomDate = () => {
  const currentDate = new Date();
  const twoYearsAgo = new Date();
  twoYearsAgo.setFullYear(currentDate.getFullYear() - 2);

  // Generate a random timestamp between the two dates
  const randomTimestamp = twoYearsAgo.getTime() + Math.random() * (currentDate.getTime() - twoYearsAgo.getTime());
  const randomDate = new Date(randomTimestamp);

  // Calculate the time difference in milliseconds
  const difference = currentDate.getTime() - randomDate.getTime();

  // Convert the difference to a human-readable "time ago" format
  const seconds = Math.floor(difference / 1000);
  const minutes = Math.floor(seconds / 60);
  const hours = Math.floor(minutes / 60);
  const days = Math.floor(hours / 24);
  const weeks = Math.floor(days / 7);
  const months = Math.floor(days / 30);
  const years = Math.floor(days / 365);

  if (years >= 1) return `${years} year${years > 1 ? 's' : ''} ago`;
  if (months >= 1) return `${months} month${months > 1 ? 's' : ''} ago`;
  if (weeks >= 1) return `${weeks} week${weeks > 1 ? 's' : ''} ago`;
  if (days >= 1) return `${days} day${days > 1 ? 's' : ''} ago`;
  if (hours >= 1) return `${hours} hour${hours > 1 ? 's' : ''} ago`;
  if (minutes >= 1) return `${minutes} minute${minutes > 1 ? 's' : ''} ago`;
  
  return `${seconds} second${seconds > 1 ? 's' : ''} ago`;
};
