import {
  ArrowUDownLeft,
  ArrowUUpRight,
  IconContext,
  Upload,
} from "@phosphor-icons/react";
import { FC } from "react";
import { navIconSetup } from "../panels/MainMenuBottomNav";
import { usePagesStore } from "../stores/pagesStore";

const PageEditor: FC = () => {
  const pagesStore = usePagesStore((state) => state);
  const {
    activePage,
    originPages,
    pages,
    undo,
    redo,
    handleRedo,
    handleUndo,
    publishPage,
  } = pagesStore;

  const isPublishable =
    JSON.stringify(originPages[activePage]) !==
    JSON.stringify(pages[activePage]);

  return (
    <IconContext.Provider value={navIconSetup}>
      <div className="rec-page-editor-container rec-editor-container">
        <div className="rec-editor-buttons rec-button-container-secondary">
          <button onClick={handleUndo} title="Undo" disabled={!undo.length}>
            <ArrowUDownLeft />
            <div className="rec-button-title">Undo</div>
          </button>
          <button onClick={handleRedo} title="Redo" disabled={!redo.length}>
            <ArrowUUpRight />
            <div className="rec-button-title">Redo</div>
          </button>
          <button
            onClick={publishPage}
            title="Publish"
            disabled={!isPublishable}
          >
            <Upload />
            <div className="rec-button-title">Publish page</div>
          </button>
        </div>
      </div>
    </IconContext.Provider>
  );
};

export default PageEditor;
