import { useEffect, useRef, useState } from "react";
import { getDistance, usePageId, randomMap } from "../utils";
import { Point, Rect, SectionData } from "../types";
import Logo from "../components/Logo";
import { usePagesStore } from "../stores/pagesStore";
import PageEditor from "../components/PageEditor";
import { useStatusStore } from "../stores/statusStore";
import { Pages } from "../pages";

const SideSwitch = () => {
  const { infoSide, sectionDetails } = useStatusStore();
  const pageId: string = usePageId(Pages);
  const isInfoSide = !!infoSide[sectionDetails];
  const sectionData: SectionData[] | undefined = usePagesStore((state) => state.pages[pageId]);

  const isEditorMode = usePagesStore((state) => !!state.editorMode);

  const onClick = (evt: React.MouseEvent | MouseEvent) => {
    evt.stopPropagation();
    /* if (!isInfoSide) {
      useStatusStore.getState().setInfoSide(sectionDetails, true);
    } else {
      useStatusStore.getState().setInfoSide(sectionDetails, false);
    } */
    sectionData.forEach((section) => {
      if (section.id) {
        useStatusStore.getState().setInfoSide(section.id, !isInfoSide);
      }
    });
  };

  return (
    <>
      {isEditorMode ? <PageEditor /> : null}
      <div className="rec-side-switch">
        <div className="rec-side-switch-logo-click-area" onClick={onClick}></div>
        <div className="rec-side-switch-logo">
          <Logo />
        </div>
      </div>
    </>
  );
};

export default SideSwitch;
