import { FC, useEffect, useRef, useState } from "react";
import { SectionData } from "../types";
import { usePagesStore } from "../stores/pagesStore";
import {
  CaretDoubleDown,
  CaretDoubleUp,
  Gear,
  IconContext,
  RowsPlusBottom,
  RowsPlusTop,
  Trash,
} from "@phosphor-icons/react";
import { navIconSetup } from "../panels/MainMenuBottomNav";
import PageSectionSettings from "./PageSectionSettings";
import { scrollContainerId } from "../utils";

const PageSectionEditor: FC<{ pageId: string; sectionId: string }> = ({
  pageId,
  sectionId,
}) => {
  const sections: SectionData[] = usePagesStore((state) => state.pages[pageId]);
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);

  const scrollContainer = useRef<HTMLElement | null>();
  const editorContainerRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    scrollContainer.current = document.getElementById(scrollContainerId);
    scrollContainer.current?.addEventListener("scroll", onContainerScroll);
    return () =>
      scrollContainer.current?.removeEventListener("scroll", onContainerScroll);
  }, []);

  const mapValue = (
    value: number,
    minA: number,
    maxA: number,
    minB: number,
    maxB: number
  ) =>
    Math.max(
      minB,
      Math.min(maxB, ((value - minA) / (maxA - minA)) * (maxB - minB) + minB)
    );

  const onContainerScroll = (evt: any) => {
    const bounds = editorContainerRef.current?.getBoundingClientRect();
    if (editorContainerRef.current && bounds) {
      const opacityValue = mapValue(
        bounds.y,
        window.innerHeight * 0.45,
        window.innerHeight * 0.65,
        0,
        1
      );

      editorContainerRef.current.style.opacity = `${opacityValue}`;
      editorContainerRef.current.style.visibility = opacityValue > 0.001 ?  'visible' : 'hidden';
    }
  };

  const section: SectionData | undefined = sections.filter(
    (part) => part.id === sectionId
  )[0];

  const moveUp = () => {
    usePagesStore.getState().moveSection("up", pageId, sectionId);
  };

  const moveDown = () => {
    usePagesStore.getState().moveSection("down", pageId, sectionId);
  };

  const newBefore = () => {
    usePagesStore.getState().newSection("up", pageId, sectionId);
  };

  const newAfter = () => {
    usePagesStore.getState().newSection("down", pageId, sectionId);
  };

  const deleteSection = () => {
    usePagesStore.getState().deleteSection(pageId, sectionId);
  };

  const toggleSettings = () => setIsSettingsOpen((x) => !x);

  return section ? (
    <>
      <IconContext.Provider value={navIconSetup}>
        <div
          className="rec-page-section-editor-container rec-editor-container"
          ref={editorContainerRef}
        >
          <div className="rec-editor-buttons rec-button-container-secondary">
            <button title="Edit values" onClick={toggleSettings}>
              <Gear />
              <div className="rec-button-title">Section settings</div>
            </button>
            <button onClick={moveUp} title="Move up">
              <CaretDoubleUp />
              <div className="rec-button-title">Move up</div>
            </button>
            <button onClick={moveDown} title="Move down">
              <CaretDoubleDown />
              <div className="rec-button-title">Move down</div>
            </button>
            <button onClick={newBefore} title="New before">
              <RowsPlusTop />
              <div className="rec-button-title">New above</div>
            </button>
            <button onClick={newAfter} title="New after">
              <RowsPlusBottom />
              <div className="rec-button-title">New below</div>
            </button>
            <button onClick={deleteSection} title="Delete">
              <Trash />
              <div className="rec-button-title">Delete section</div>
            </button>
          </div>
        </div>
      </IconContext.Provider>
      <PageSectionSettings
        pageId={pageId}
        sectionId={sectionId}
        onClose={toggleSettings}
        open={isSettingsOpen}
      />
    </>
  ) : null;
};

export default PageSectionEditor;
