import { FC, useState } from "react";
import ReactMarkdown from "react-markdown";
import { useStatusStore } from "../stores/statusStore";
import { uniqueNamesGenerator, names, starWars } from "unique-names-generator";
import Popup from "./Popup";

import { getAuthorElement, usePageId, onFullscreen } from "../utils";
import { Pages } from "../pages";
import { SectionData, SectionType, Tab } from "../types";
import { usePagesStore } from "../stores/pagesStore";
import InstructionLine from "./InstructionLine";
import { BookOpen, Chat, HandEye, HandHeart, Heart, Info, ShareFat, ShieldWarning, Star, TextOutdent, User } from "@phosphor-icons/react";
import Avatar from "boring-avatars";
import Likes from "./Likes";
import Comments from "./Comments";
import PopupButton from "./PopupButton";

enum ArtworkTab {
  INSTRUCTION = "instruction",
  DESCRIPTION = "description",
  LIKE = "like",
  DISCUSSION = "discussion",
  ARTIST = "artist",
  MENU = "menu",
}

const tempCount = () => Math.ceil(Math.random() * 100) * (Math.random() < 0.5 ? 10 : 1) * (Math.random() < 0.5 ? 100 : 1);

const tabs: Tab[] = [
  { id: ArtworkTab.DESCRIPTION, name: "Description" },
  { id: ArtworkTab.LIKE, name: "Likes", count: tempCount() },
  { id: ArtworkTab.DISCUSSION, name: "Discussions", count: tempCount() },
  { id: ArtworkTab.ARTIST, name: "Artists" },
  { id: ArtworkTab.MENU, name: "Artwork Menu" },
];

const getTabCount = (count: number | undefined): string => {
  if (!count) {
    return "0";
  }
  if (count > 999) {
    return `${Math.floor(count / 1000)}k+`;
  }
  return count.toString();
};

const getIcon = (icon: ArtworkTab, color: string = "white", iconSize: number = 20): JSX.Element => {
  switch (icon) {
    case ArtworkTab.INSTRUCTION:
      return <Info size={iconSize} color={color} />;
    case ArtworkTab.DESCRIPTION:
      return <BookOpen size={iconSize} color={color} />;
    case ArtworkTab.LIKE:
      return <Heart size={iconSize} color={color} />;
    case ArtworkTab.DISCUSSION:
      return <Chat size={iconSize} color={color} />;
    case ArtworkTab.ARTIST:
      return <User size={iconSize} color={color} />;
    case ArtworkTab.MENU:
      return <TextOutdent size={iconSize} color={color} />;
    default:
      return <HandEye size={iconSize} color={color} />;
  }
};

const ArtworkLaunchPopup: FC<{ open?: boolean; id?: string; onClose?: () => void }> = ({ open, id, onClose }) => {
  const [sectionDetails] = useStatusStore((state) => [state.artworkLaunch, state.sectionDetails]);
  const sectionId = id ?? sectionDetails;
  const pageId: string = usePageId(Pages);
  const sectionData: SectionData[] | undefined = usePagesStore((state) => state.pages[pageId]);

  const [activeTab, setActiveTab] = useState(ArtworkTab.DESCRIPTION);

  const details = Object.values(sectionData ?? {}).reduce(
    (acc: SectionData | undefined, section) => (section.id === sectionId ? section : acc),
    undefined
  );

  const { authors, title, type, instructions } = details ?? {};

  const filteredTabs = [...tabs];

  //marble, beam, pixel, sunset, ring and bauhaus.
  return (
    <div
      className={`rec-popup-backdrop ${open ? "rec-open-panel" : "rec-closed-panel"}`}
      onClick={(evt) => {
        if ((evt.target as HTMLDivElement).classList.contains("rec-popup-backdrop")) {
          onClose?.();
        }
      }}
    >
      <div className="rec-popup-container">
        <div className="rec-popup-wrapper">
          <div className="rec-popup-title">{title}</div>
          <div className="rec-popup-authors">
            {authors && !!authors.length ? "by " : ""}
            {getAuthorElement(authors)}
          </div>
          <div className="rec-popup-tab-bar">
            <div className="rec-tab-bar">
              {filteredTabs.map((tab) => {
                const { id, name, count } = tab;

                return (
                  <button
                    key={id}
                    className={`rec-tab-button${activeTab === id ? " rec-active" : ""}`}
                    onClick={() => setActiveTab(id as ArtworkTab)}
                  >
                    <div className="rec-tab-button-icon">{getIcon(id as ArtworkTab)}</div>
                    {!!count && <div className="rec-tab-button-text">{getTabCount(count)}</div>}
                  </button>
                );
              })}
            </div>
          </div>
          <div className="rec-popup-content">
            {[activeTab].map((tab) => {
              switch (tab) {
                default:
                case ArtworkTab.INSTRUCTION:
                  return (
                    <div key={tab} className="rec-popup-content-full">
                      <div className="rec-popup-details">{instructions}</div>
                    </div>
                  );
                case ArtworkTab.DESCRIPTION:
                  return (
                    <div key={tab} className="rec-popup-content-full rec-markdown">
                      <ReactMarkdown>{`
  ## What is this ${type === SectionType.Artwork ? "artwork" : "exhibition"} all about?

  In the last decade, motion graphics has become a huge field for creativity, as technology and resources are available to almost anyone who wants to learn the basics of modern animation. Animator Markus Magnusson is a well-known face in this market with a rich portfolio and over 15 years of experience. He actively shares his knowledge through author’s animation courses. And in 2021, he began to comprehend the NFT market, popularizing his work. Today, he is known to many thanks to his sensational Invisible Friends collection. How did he do it? And what is unique about his work? Let’s find out.
  
  ![Running](https://i.pinimg.com/originals/7f/e5/b5/7fe5b527c821e876ccd5bc9d4a448505.gif)

  In his youth, [Markus Magnusson](https://www.instagram.com/motionmarkus/) could hardly imagine himself as a well-known creative person. But everything changed when, at the age of 22, while working as a cook, he bought a semi-professional video camera with a 3ccd chip. This led to the fact that he entered the media school and began to comprehend the basics of graphics and further animation.
  
  ![Rollin](https://i.pinimg.com/originals/74/bd/5b/74bd5bb152c871427592f289e9f19c35.gif)

  Since that time, he began to carry out at first small graphic orders, gradually increasing his professionalism. At some point, his skill reached the level where large and very large brands want to work with the artist.

  ![Rollin 2](https://weandthecolor.com/wp-content/uploads/2016/04/Kick-Push-a-funny-animated-GIF-by-Swedish-graphic-designer-and-illustrator-Markus-Magnusson.gif)
`}</ReactMarkdown>

                      <div className="rec-popup-instructions">
                        <h2>Interactions:</h2>
                        <div className="rec-popup-instruction-lines">
                          <InstructionLine icon="camera">Give permissions for the camera</InstructionLine>
                          <InstructionLine icon="click">Click anywhere on the artwork</InstructionLine>
                        </div>
                      </div>
                    </div>
                  );
                case ArtworkTab.LIKE:
                  return (
                    <div key={tab} className="rec-popup-content-full rec-markdown">
                      <PopupButton
                        text="Like Artwork"
                        onClick={() => {
                          console.log("TODO");
                        }}
                        Icon={Heart}
                      />
                      <PopupButton
                        text="Add to favorites"
                        onClick={() => {
                          console.log("TODO");
                        }}
                        Icon={Star}
                      />
                      <PopupButton
                        text="Share Artwork"
                        onClick={() => {
                          console.log("TODO");
                        }}
                        Icon={ShareFat}
                      />
                      <div className="rec-popup-sub-title">People also liked:</div>
                      <Likes sectionData={details} overflowAuto icon="heart" />
                    </div>
                  );
                case ArtworkTab.ARTIST:
                  return (
                    <div key={tab} className="rec-popup-content-full rec-markdown">
                      <div className="rec-popup-sub-title">Creators:</div>
                      <Likes sectionData={details} debugItems={[{ name: "Markus Magnusson" }]} />
                      <div className="rec-popup-sub-title">Technical Support:</div>
                      <Likes sectionData={details} debugItems={[{ name: "Joseph Peterson" }, { name: "The Recursive Collective" }]} />
                      <div className="rec-popup-sub-title">Sponsors:</div>
                      <Likes
                        sectionData={details}
                        debugItems={[{ name: "Peter Amidala" }, { name: "Bovril UK" }, { name: "timeparadox 22" }]}
                      />
                    </div>
                  );
                case ArtworkTab.MENU:
                  return (
                    <div key={tab} className="rec-popup-content-full rec-markdown">
                      <PopupButton
                        text="Be a sponsor of this Artwork"
                        onClick={() => {
                          console.log("TODO");
                        }}
                        Icon={HandHeart}
                      />
                      <PopupButton
                        text="Contact us about this content"
                        onClick={() => {
                          console.log("TODO");
                        }}
                        Icon={ShieldWarning}
                      />
                    </div>
                  );

                case ArtworkTab.DISCUSSION:
                  return <Comments sectionData={details} />;
              }
            })}
          </div>
        </div>
        <button
          className="rec-popup-button rec-main-artwork-button"
          onClick={() => {
            if (type === SectionType.Artwork) {
              useStatusStore.getState().setArtworkShow(sectionId);
              useStatusStore.getState().setArtworkLaunch(null);
              onFullscreen(true);
            }
          }}
        >
          {type === SectionType.Artwork ? "Run Artwork" : "Visit Exhibition"}
        </button>
      </div>
    </div>
  );
};
//   <Popup onClose={() => useStatusStore.getState().setArtworkLaunch(null)}>
export default ArtworkLaunchPopup;
