import { FC, useRef, useState } from "react";
import { SectionData } from "../types";
import SlidePanel, { Position } from "./SlidePanel";
import { debugRandomDate, getAuthorElement } from "../utils";
import tempPic from "../../images/temp_info.png";
import { names, starWars, uniqueNamesGenerator } from "unique-names-generator";
import Avatar from "boring-avatars";
import { DotsThreeOutline, Heart, ShareFat } from "@phosphor-icons/react";
import { loremIpsum } from "lorem-ipsum";

interface Props {
  text?: string;
  selected?: string;
  Icon?: any;
  onClick?: () => void;
}

const PopupButton: FC<Props> = ({ selected, Icon, onClick, text }) => {
  const [hover, setHover] = useState(false);

  return (
    <button
      className="rec-popup-button rec-icon-button"
      onClick={onClick}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
    >
      {Icon ? <Icon color="var(--secondaryColor)" size="20px" weight={selected || hover ? "fill" : "regular"} /> : null}
      {text}
    </button>
  );
};

export default PopupButton;
