import { FC, useRef } from "react";
import { SectionData } from "../types";
import SlidePanel, { Position } from "./SlidePanel";
import { debugRandomDate, getAuthorElement } from "../utils";
import tempPic from "../../images/temp_info.png";
import { names, starWars, uniqueNamesGenerator } from "unique-names-generator";
import Avatar from "boring-avatars";
import { CaretDown, CaretUp, DotsThreeOutline, Heart } from "@phosphor-icons/react";
import { loremIpsum } from "lorem-ipsum";

interface Props {
  sectionData: SectionData | undefined;
  debugItems?: { name: string; text: string }[];
  overflowAuto?: boolean;
  icon?: "heart";
}

const Comments: FC<Props> = ({ sectionData, debugItems, overflowAuto, icon }) => {
  const arr = debugItems ? debugItems : new Array(Math.round(Math.random() * 100)).fill(null);

  const comments = useRef(
    arr.map((item) => {
      if (item) return item;
      const shortName: string = uniqueNamesGenerator({
        dictionaries: [names],
      });
      const shortName2: string = uniqueNamesGenerator({
        dictionaries: [starWars],
      });

      return {
        name: `${shortName} ${shortName2.split(" ").pop()}`,
        text: loremIpsum(),
        time: debugRandomDate(),
      };
    })
  );

  return (
    <div className="rec-avatar-list" style={{ overflowY: overflowAuto ? "auto" : "unset" }}>
      {comments.current.map(({ name: uname, text, time }) => {
        return (
          <div key={uname + time} className="rec-comment">
            <div className="rec-avatar">
              <div className="rec-avatar-container">
                <Avatar name={uname} variant="marble" />
                {icon === "heart" ? <Heart className="rec-avatar-icon" color="var(--secondaryColor)" size="22px" weight="fill" /> : null}
              </div>
              {uname}
              <div className="rec-avatar-suffix">
                {time}
                <CaretUp size={16} color="var(--primaryColor)" />
                <CaretDown size={16} color="var(--primaryColor)" />
                <DotsThreeOutline color="var(--primaryColor)" size={16} weight="fill" />
              </div>
            </div>
            <div className="rec-comment-text">{text}</div>
          </div>
        );
      })}
    </div>
  );
};

export default Comments;
