import { FC, useEffect } from "react";
import { useStatusStore } from "../stores/statusStore";
import Popup from "./Popup";
import { fullscreenOff, getAuthorElement, usePageId, onFullscreen } from "../utils";
import { Pages } from "../pages";
import { SectionData } from "../types";
import { usePagesStore } from "../stores/pagesStore";
import InstructionLine from "./InstructionLine";
import IFrame from "./IFrame";

const ArtworkPopup: FC = () => {
  const [artworkShow, sectionDetails] = useStatusStore((state) => [state.artworkShow, state.sectionDetails]);
  const pageId: string = usePageId(Pages);
  const sectionData: SectionData[] | undefined = usePagesStore((state) => state.pages[pageId]);

  const details = Object.values(sectionData ?? {}).reduce(
    (acc: SectionData | undefined, section) => (section.id === sectionDetails ? section : acc),
    undefined
  );

  const { authors, title, type, content } = details ?? {};

  useEffect(() => {
    document.addEventListener("fullscreenchange", handleFullscreenChange);

    return () => {
      document.removeEventListener("fullscreenchange", handleFullscreenChange);
    };
  }, []);

  const handleFullscreenChange = () => {
    if (!document.fullscreenElement) {
      useStatusStore.getState().setArtworkShow(null);
    }
  };

  const closeArtwork = () => {
    if (document.fullscreenElement) {
      fullscreenOff();
    } else {
      useStatusStore.getState().setArtworkShow(null);
    }
  };

  return !artworkShow ? null : (
    <Popup fullscreen={true}>
      {typeof content === "string" ? <IFrame url={content} onDoubleClick={closeArtwork} autoStart={true} /> : content}
    </Popup>
  );
};

export default ArtworkPopup;
