import {
  CornersIn,
  CornersOut,
  Gear,
  IconContext,
  IconProps,
  SignOut,
} from "@phosphor-icons/react";
import { useEffect, useState } from "react";
import { usePagesStore } from "../stores/pagesStore";
import { isFullscreen, toggleFullScreen } from "../utils";

export const navIconSetup = {
  color: "var(--color)",
  size: 22,
  weight: "regular",
} as IconProps;

const MainMenuBottomNav = () => {
  const [, setRender] = useState(0);
  const editorMode = usePagesStore((state) => state.editorMode);
  const user = null;

  useEffect(() => {
    document.addEventListener("fullscreenchange", onFullscreenChange);
    return () =>
      document.removeEventListener("fullscreenchange", onFullscreenChange);
  }, []);

  const onFullscreenChange = () => setRender((x) => x + 1);

  const onEditorMode = () => {
    usePagesStore.getState().toggleEditorMode();
  };

  return (
    <IconContext.Provider value={navIconSetup}>
      <div className="rec-panel-bottom-menu">
        <div className="rec-editor-buttons rec-button-container-secondary">
          <button
            key="Fullscreen"
            title="Fullscreen"
            onClick={toggleFullScreen}
          >
            {isFullscreen() ? <CornersIn /> : <CornersOut />}
            <div className="rec-button-title">Fullscreen</div>
          </button>
          <button key="Editor mode" title="Editor mode" onClick={onEditorMode}>
            {editorMode ? <SignOut /> : <Gear />}
            <div className="rec-button-title">Editor mode</div>
          </button>
        </div>
      </div>
    </IconContext.Provider>
  );
};

export default MainMenuBottomNav;
