import { CaretDown, CaretUp, Info, List, ProjectorScreen, SignIn, SignOut } from "@phosphor-icons/react";
import SlidePanel from "./components/SlidePanel";
import { useStatusStore } from "./stores/statusStore";
import BreadcrumbSpacer from "./components/BreadcrumbSpacer";
import { FC, useEffect, useRef } from "react";
import { getAuthorElement, usePageId, getSectionActionIcon, headerIconSize, onFullscreen, scrollContainerId } from "./utils";
import { PageData, SectionData, SectionType } from "./types";
import { usePagesStore } from "./stores/pagesStore";
import { useLocation } from "react-router-dom";
import { Pages } from "./pages";

const Header = () => {
  const { infoSide, isMenuOpen, sectionDetails } = useStatusStore();
  const pageId: string = usePageId(Pages);
  const sectionData: SectionData[] | undefined = usePagesStore((state) => state.pages[pageId]);
  const sectionValues = Object.values(sectionData ?? {});
  const sectionIdx = sectionValues.reduce(
    (acc: number | undefined, section, idx) => (section.id === sectionDetails ? idx : acc),
    undefined
  );
  const headerRef = useRef<HTMLDivElement>(null);

  const isInfoSide = !!infoSide[sectionDetails];

  const details = Object.values(sectionData ?? {}).reduce(
    (acc: SectionData | undefined, section) => (section.id === sectionDetails ? section : acc),
    undefined
  );

  const { authors, title, type } = details ?? {};

  const faderRef = useRef<HTMLDivElement | null>(null);
  const mobileBreadcrumbRef = useRef<HTMLDivElement | null>(null);
  const scrollRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    if (!scrollRef.current) {
      scrollRef.current = document.querySelector(".rec-main-scroll-container");
    }
    const handleScroll = () => {
      if (scrollRef.current && faderRef.current && sectionData && mobileBreadcrumbRef.current) {
        const { scrollTop, clientHeight } = scrollRef.current;
        1;
        const opacityPercentage = Object.keys(sectionData).reduce((acc, _, idx) => {
          const top = clientHeight * idx;
          const bottom = clientHeight * (idx + 1);
          if (scrollTop > top && scrollTop < bottom) {
            const percentage = (scrollTop - top) / clientHeight;
            const progress = percentage;

            if (progress < 0.5) {
              return 1 - progress * 2;
            } else {
              return (progress - 0.5) * 2;
            }
          }
          return acc;
        }, 1);

        faderRef.current.style.opacity = `${opacityPercentage}`;
        //mobileBreadcrumbRef.current.style.opacity = `${opacityPercentage}`;
        const mobileItems = document.querySelectorAll(".rec-mobile-breadcrumb-item");
        mobileItems.forEach((element) => {
          (element as HTMLDivElement).style.transform = `translateX(${-100 * (1 - opacityPercentage)}%)`;
          (element.firstChild as HTMLDivElement).style.transform = `translateX(${100 * (1 - opacityPercentage)}%)`;
        });
      }
    };

    const currentRef = scrollRef.current;
    if (currentRef) {
      currentRef.addEventListener("scroll", handleScroll);
      return () => currentRef.removeEventListener("scroll", handleScroll);
    }
  }, []);

  /* useEffect(() => {
    const handleScroll = () => {
      const scrollable = document.querySelector(`#${scrollContainerId}`)
      if (headerRef.current && scrollable) {
        const headerBottom = headerRef.current.getBoundingClientRect().bottom;
        
        // Only scroll the div if we've scrolled past the header
        if (window.scrollY >= headerBottom) {
          const scrollOffset = window.scrollY - headerBottom;
          scrollable.scrollTop = scrollOffset;
        } else {
          scrollable.scrollTop = 0;
        } 
      }
    };

    window.addEventListener('wheel', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []); */

  const Wrapper: FC<{ mobile?: boolean; children: React.ReactNode }> = ({ mobile, children }) =>
    mobile ? (
      <div className="rec-mobile-breadcrumb-item">
        <div className="rec-mobile-breadcrumb-item-slider">{children}</div>
      </div>
    ) : (
      <span>{children}</span>
    );

  const BreadcrumbSection: FC<{ mobile?: boolean }> = ({ mobile }) => (
    <>
      <Wrapper key="origin" mobile={mobile}>
        <a href="/">Recursive&nbsp;Gallery</a>
        <BreadcrumbSpacer short />
      </Wrapper>
      <Wrapper  key="page" mobile={mobile}>
        <a href="/">Explore</a>
        <BreadcrumbSpacer short />
      </Wrapper>
    </>
  );

  const ArtworkSection: FC<{ mobile?: boolean }> = ({ mobile }) =>
    authors || title ? (
      <>
        {authors ? (
          <Wrapper key="author" mobile={mobile}>
            {getAuthorElement(authors)}
            {title ? <BreadcrumbSpacer alt /> : null}
          </Wrapper>
        ) : null}
        {title ? (
          <Wrapper key="title" mobile={mobile}>
            <a>{title}</a>
          </Wrapper>
        ) : null}
      </>
    ) : null;

  return (
    <>
      <header className={`rec-header${isInfoSide ? " rec-closed" : ""}`} ref={headerRef}>
        <div className="rec-header-container">
          <div className="rec-header-breadcrumb">
            <div className="rec-header-breadcrumb-base">
              <BreadcrumbSection />
            </div>
            <div className="rec-header-fade-container" ref={faderRef}>
              <div className="rec-header-fade-text-container">
                <div className="rec-header-fade-text">
                  <ArtworkSection />
                </div>
              </div>
              <div style={{ marginLeft: "auto" }}></div>
              {type !== SectionType.Any && type ? (
                <button
                  className="rec-menu-button"
                  onClick={() => {
                    if (type === SectionType.Artwork) {
                      useStatusStore.getState().setArtworkShow(sectionDetails);
                      onFullscreen(true);
                    }
                    if (type === SectionType.Exhibition) {
                      window.location.href = "/";
                    }
                  }}
                >
                  {getSectionActionIcon(type)}
                </button>
              ) : null}
              <button className="rec-menu-button" onClick={() => useStatusStore.getState().setInfoSide(sectionDetails, !isInfoSide)}>
                {isInfoSide ? (
                  <>
                    <Info size={headerIconSize} />
                    <div className="rec-icon-cross"></div>
                  </>
                ) : (
                  <Info size={headerIconSize} />
                )}
              </button>
            </div>

            <button className={`rec-menu-button${sectionIdx === 0 ? " rec-disabled" : ""}`} onClick={useStatusStore.getState().setScrollUp}>
              <CaretUp size={headerIconSize * 1.1} color="var(--primaryColor)" />
            </button>
            <button
              className={`rec-menu-button${sectionIdx === sectionValues.length - 1 ? " rec-disabled" : ""}`}
              onClick={useStatusStore.getState().setScrollDown}
            >
              <CaretDown size={headerIconSize * 1.1} color="var(--primaryColor)" />
            </button>
            <button className="rec-menu-button rec-menu-button-margin" onClick={() => useStatusStore.getState().setMenuOpen(!isMenuOpen)}>
              <List size={headerIconSize * 1} color="var(--primaryColor)" />
            </button>
          </div>
        </div>
      </header>
      <div key="rec-mobile-breadcrumb" className={`rec-mobile-breadcrumb${isInfoSide ? " rec-hidden" : ""}`} ref={mobileBreadcrumbRef}>
        <div className="rec-mobile-breadcrumb-panel">
          <BreadcrumbSection mobile />
          <ArtworkSection mobile />
        </div>
      </div>
    </>
  );
};
export default Header;
