import React, { FC, useEffect, useRef, useState } from "react";
import { useStatusStore } from "../stores/statusStore";
import { usePageId } from "../utils";
import { Pages } from "../pages";
import { SectionData } from "../types";
import { usePagesStore } from "../stores/pagesStore";

interface Props {}

enum LogoState {
  OFF = "off",
  DEFAULT = "default",
  INFO = "info",
  MENU = "menu",
}

interface LogoBitTransform {
  x?: number;
  y?: number;
  rotation?: number;
}
const logoElementTransforms: { [id: string]: LogoBitTransform[] } = {
  [LogoState.OFF]: [
    { x: 10, y: 0 },
    { x: -50, y: 0, rotation: 180 },
    { x: 62, y: 0 },
    { x: -102, y: 0, rotation: 180 },
  ],
  [LogoState.DEFAULT]: [
    { x: 0, y: 0 },
    { x: -40, y: 0, rotation: 180 },
    { x: 32, y: 0 },
    { x: -72, y: 0, rotation: 180 },
  ],
  /* [LogoState.INFO]: [
    { x: 0, y: 0, rotation: -180 },
    { x: -40, y: 0 },
    { x: 32, y: 0, rotation: 180 },
    { x: -72, y: 0 },
  ],*/
  [LogoState.INFO]: [
    { x: -40, y: 0 },
    { x: 0, y: 0, rotation: 180 },
    { x: -72, y: 0 },
    { x: 32, y: 0, rotation: 180 },
  ],
  [LogoState.MENU]: [],
};

const Logo: FC<Props> = () => {
  const [_, setRender] = useState(0);
  const { infoSide, sectionDetails } = useStatusStore();
  const isInfoSide = !!infoSide[sectionDetails];
  const inited = useRef(false);
  const defaultTransform = useRef([...logoElementTransforms[LogoState.DEFAULT]]);
  const infoTransform = useRef([...logoElementTransforms[LogoState.INFO]]);
  const [logoState, setLogoState] = useState(LogoState.OFF);
  const logoBitRef = useRef<(SVGSVGElement | null)[]>(logoElementTransforms[LogoState.DEFAULT].map(() => null));
  const scumble = (arr: LogoBitTransform[]) => {
    return arr.sort((a, b) => (Math.random() < 0.5 ? -1 : 1));
  };

  const applyTransforms = (arr: LogoBitTransform[]) => {
    logoBitRef.current.forEach((element, idx) => {
      if (arr[idx] && element) {
        const { x, y, rotation } = arr[idx];
        element.style.transform = `translate(${x}px, ${y}px)${rotation ? ` rotate(${rotation}deg)` : ""}`;
      }
    });
    //  transform:
  };

  const onLogoBit = (ref: SVGSVGElement | null, id: number) => {
    if (ref) {
      logoBitRef.current[id] = ref;
    }
  };

  useEffect(() => {
    let state = LogoState.DEFAULT;
    if (!inited.current) {
      inited.current = true;
    } else {
      if (isInfoSide) {
        state = LogoState.INFO;
      } else {
        state = LogoState.DEFAULT;
      }
    }

    if (state === LogoState.DEFAULT) {
      defaultTransform.current = scumble(defaultTransform.current);
    }
    if (state === LogoState.INFO) {
      infoTransform.current = scumble(infoTransform.current);
    }
    const newTransform =
      state === LogoState.DEFAULT
        ? defaultTransform.current
        : state === LogoState.INFO
        ? infoTransform.current
        : logoElementTransforms[state];

    applyTransforms([...newTransform]);
    setLogoState(state);
  }, [isInfoSide, sectionDetails]);

  return (
    <>
      {defaultTransform.current.map((_, idx) => {
        return (
          <svg
            key={idx}
            viewBox="0 0 40 53"
            className="rec-logo-svg-element"
            style={{
              opacity: logoState === LogoState.OFF ? 0 : 1,
            }}
            ref={(ref) => onLogoBit(ref, idx)}
          >
            <g>
              <polygon
                className="rec-logo-svg-fill"
                points="39.9 13.6 32.1 0 16.1 0 .1 26.5 16.1 53 32.1 53 39.9 39.4 32.1 26.5 39.9 13.6"
              />

              <g>
                <line className="rec-logo-svg-outline" x1=".1" y1="26.5" x2="16.1" y2="26.5" />
                <g>
                  <polyline className="rec-logo-svg-outline" points="32.1 53 16.1 26.5 32.1 0" />
                  <polygon
                    className="rec-logo-svg-outline"
                    points=".1 26.5 16.1 53 32.1 53 39.9 39.4 32.1 26.5 39.9 13.6 32.1 0 16.1 0 .1 26.5"
                  />
                </g>
              </g>
            </g>
          </svg>
        );
      })}
    </>
  );
};

export default Logo;
