import { create } from "zustand";
import { PageData, SectionData, SectionType } from "../types";
import { cloneDeep } from "lodash-es";
import { getId } from "../utils";

export const SectionTypeName = {
  [SectionType.Any]: "",
  [SectionType.Invitation]: "Exhibition Invitation",
  [SectionType.Exhibition]: "Exhibition",
  [SectionType.Artwork]: "Artwork",
}

interface PagesStore {
  originPages: PageData;
  pages: PageData;
  editorMode: boolean;
  editedSectionId: string;
  sourceSectionId: string;
  editCount: number;
  undo: SectionData[][];
  redo: SectionData[][];
  activePage: string;
  setActivePage: (pageId: string) => void;
  setPages: (name: string, data: SectionData[]) => void;
  setAllPages: (data: PageData) => void;
  setOriginPages: (data: PageData) => void;
  moveSection: (direction: "up" | "down", pageId: string, sectionId: string) => void;
  moveSectionInternal: (direction: "up" | "down", pageId: string, sectionId: string) => void;
  newSection: (direction: "up" | "down", pageId: string, sectionId: string) => void;
  newSectionInternal: (direction: "up" | "down", pageId: string, sectionId: string) => void;
  deleteSection: (pageId: string, sectionId: string) => void;
  deleteSectionInternal: (pageId: string, sectionId: string) => void;
  setEditorMode: (state: boolean) => void;
  toggleEditorMode: () => void;
  addUndo: () => void;
  handleUndo: () => void;
  handleRedo: () => void;
  publishPage: () => void;
}

export const usePagesStore = create<PagesStore>((set, get) => ({
  originPages: {},
  pages: {},
  editorMode: false,
  editedSectionId: "",
  sourceSectionId: "",
  editCount: 0,
  undo: [],
  redo: [],
  activePage: "",
  setActivePage: (pageId: string) =>
    set((state) => ({
      activePage: pageId,
      undo: [],
      redo: [],
      pages: cloneDeep(state.originPages),
    })),
  setPages: (name: string, data: SectionData[]) =>
    set((state: PagesStore) => ({
      pages: { ...state.pages, [name]: data },
    })),
  setAllPages: (data: PageData) =>
    set(() => ({
      pages: data,
    })),
  setOriginPages: (data: PageData) =>
    set(() => ({
      originPages: data,
    })),
  setEditorMode: (state: boolean) =>
    set(() => ({
      editorMode: state,
    })),
  toggleEditorMode: () =>
    set((state: PagesStore) => ({
      editorMode: !state.editorMode,
    })),
  moveSection: (direction: "up" | "down", pageId: string, sectionId: string) => {
    get().addUndo();
    get().moveSectionInternal(direction, pageId, sectionId);
  },
  moveSectionInternal: (direction: "up" | "down", pageId: string, sectionId: string) =>
    set((state: PagesStore) => {
      const page = state.pages[pageId];
      if (!page) return {};
      const indexes: { old: number; new: number } | undefined = page.reduce(
        (acc: { old: number; new: number } | undefined, section: SectionData, idx) =>
          section.id === sectionId ? (direction === "up" ? { old: idx, new: idx - 1 } : { old: idx, new: idx + 1 }) : acc,
        undefined
      );
      if (!indexes || indexes.new < 0 || indexes.new > page.length - 1) return {};

      const sourceSectionId = page[indexes.new].id;
      const newSections = [...page];
      const temp = newSections[indexes.old];
      newSections[indexes.old] = newSections[indexes.new];
      newSections[indexes.new] = temp;
      return {
        editedSectionId: sectionId,
        sourceSectionId,
        editCount: state.editCount + 1,
        pages: { ...state.pages, [pageId]: newSections },
      };
    }),
  newSection: (direction: "up" | "down", pageId: string, sectionId: string) => {
    get().addUndo();
    get().newSectionInternal(direction, pageId, sectionId);
  },
  newSectionInternal: (direction: "up" | "down", pageId: string, sectionId: string) =>
    set((state: PagesStore) => {
      const page = state.pages[pageId];
      if (!page) return {};
      const newId = getId();

      const newSections = page.reduce((acc: SectionData[], data: SectionData) => {
        if (data.id === sectionId) {
          const newData = cloneDeep(data);
          newData.id = newId;
          if (direction === "up") {
            return [...acc, newData, data];
          } else {
            return [...acc, data, newData];
          }
        }
        return [...acc, data];
      }, []);

      return {
        editedSectionId: newId,
        sourceSectionId: sectionId,
        editCount: state.editCount + 1,
        pages: { ...state.pages, [pageId]: newSections },
      };
    }),
  deleteSection: (pageId: string, sectionId: string) => {
    get().addUndo();
    get().deleteSectionInternal(pageId, sectionId);
  },
  deleteSectionInternal: (pageId: string, sectionId: string) =>
    set((state: PagesStore) => {
      const page = state.pages[pageId];
      if (!page) return {};
      return {
        pages: {
          ...state.pages,
          [pageId]: page.filter((p) => p.id !== sectionId),
        },
      };
    }),
  addUndo: () =>
    set((state: PagesStore) => {
      const { pages, activePage, undo } = state;
      if (!pages[activePage]) return {};
      return {
        undo: [...undo, cloneDeep(pages[activePage]) as SectionData[]],
        redo: [],
      };
    }),
  handleUndo: () =>
    set((state: PagesStore) => {
      const { pages, activePage, undo, redo } = state;
      const data = undo[undo.length - 1];
      if (!data) return {};

      return {
        redo: [...redo, cloneDeep(pages[activePage])],
        undo: undo.slice(0, -1),
        pages: { ...pages, [activePage]: cloneDeep(data) },
      };
    }),
  handleRedo: () =>
    set((state: PagesStore) => {
      const { pages, activePage, undo, redo } = state;
      const data = redo[redo.length - 1];
      if (!data) return {};

      return {
        undo: [...undo, cloneDeep(pages[activePage])],
        redo: redo.slice(0, -1),
        pages: { ...pages, [activePage]: cloneDeep(data) },
      };
    }),
  publishPage: () =>
    set((state: PagesStore) => {
      const { activePage } = state;
      const page = state.pages[activePage];
      if (!page) return {};
      return {
        originPages: {
          ...state.originPages,
          [activePage]: cloneDeep(page),
        },
      };
    }),
}));
