import { FC, useEffect, useRef } from "react";

const IFrame: FC<{
  url: string;
  autoStart?: boolean;
  paused?: boolean;
  onClick?: () => void;
  onDoubleClick?: () => void;
}> = ({ url, autoStart, paused, onClick, onDoubleClick }) => {
  const iframeWindow = useRef<Window | null>(null);
  const clickCount = useRef(0);

  const clickTimer = useRef(0);
  const clearTimer = useRef(0);
  const iframeInit = useRef(false);
  const mouseDownPos = useRef<{ x: number; y: number } | null>(null);
  const pausedRef = useRef(true);
  pausedRef.current = !!paused;

  useEffect(() => {
    if (paused) {
      iframeWindow.current?.postMessage("stop", "*");
    } else {
      iframeWindow.current?.postMessage("start", "*");
    }
  }, [paused]);

  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      if (!iframeWindow.current || event.source !== iframeWindow.current) return;

      console.log("message", event);
      if (event.data.ready) {
        onHandshake();
      }
      if (event.data.mouse) {
        if (event.data.mouse === "down") {
          handleMouseDown(event.data.position);
        }
        if (event.data.mouse === "up") {
          handleMouseUp(event.data.position);
        }
      }
    };

    window.addEventListener("message", handleMessage);
    return () => {
      window.removeEventListener("message", handleMessage);
    };
  }, []);

  const onHandshake = () => {
    iframeInit.current = true;
    if (autoStart || !pausedRef.current) {
      iframeWindow.current?.postMessage("init", "*");
      iframeWindow.current?.postMessage("start", "*");
    }
  };

  const clearClick = () => {
    if (clickCount.current > 1) {
      mouseDownPos.current = null;
      clickCount.current = 0;
    }
  };

  const handleMouseDown = (position: { x: number; y: number } | undefined) => {
    if (!position) {
      return;
    }
    clickCount.current = clickCount.current + 1;
    mouseDownPos.current = position;
    clearTimeout(clearTimer.current);
    clearTimer.current = window.setTimeout(clearClick, 220);
  };

  const handleMouseUp = (position: { x: number; y: number } | undefined) => {
    if (mouseDownPos.current) {
      const distance = Math.sqrt(
        Math.pow((position?.x ?? 0) - mouseDownPos.current.x, 4) + Math.pow((position?.y ?? 0) - mouseDownPos.current.y, 4)
      );
      if (distance <= 1) {
        if (clickCount.current <= 1) {
          onClick?.();
        }
        if (clickCount.current === 2) {
          onDoubleClick?.();
        }
        if (clickTimer.current) clearTimeout(clickTimer.current);
        clickTimer.current = window.setTimeout(() => {
          clickCount.current = 0;
        }, 220);
      } else {
        clickCount.current = 0;
        mouseDownPos.current = null;
      }
    }
  };

  return (
    <div className="iframe-container">
      <iframe
        key={url}
        src={url}
        ref={(ref: HTMLIFrameElement | null) => {
          if (ref && !iframeWindow.current) {
            iframeWindow.current = ref.contentWindow;
          }
        }}
        sandbox="allow-scripts allow-same-origin allow-orientation-lock allow-forms"
      ></iframe>
    </div>
  );
};

export default IFrame;
