import { FC } from "react";

const BreadcrumbSpacer: FC<{ alt?: boolean; short?: boolean }> = ({ alt, short }) => {
  return (
    <span className={`rec-spacer${alt ? " rec-spacer-alt" : ""}${short ? " rec-spacer-short" : ""}`}>
      {short ? "//" : alt ? ":" : ""}
    </span>
  );
};

export default BreadcrumbSpacer;
